import React from "react"
import Anime from 'react-anime'
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import 'bootstrap/dist/css/bootstrap.css';
import { Container, Row, Col } from "react-bootstrap"
import "../components/app.css"
import Layout from "../components/layout.js"
import SEO from "../components/seo"
import ProjectNav from "../components/project-nav"

const FlexablePage = () => {
  const data = useStaticQuery(graphql`
    query {
      imageOne: file(relativePath: { eq: "home/flexable.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageTwo: file(relativePath: { eq: "flexable/1caregiver-register.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageThree: file(relativePath: { eq: "flexable/2customer-events2.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageFour: file(relativePath: { eq: "flexable/3mockup-quotemodal-2.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageFive: file(relativePath: { eq: "flexable/4caregiver-profileview.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
  <Layout>
    <Container fluid={true}>
      <Row>
        <ProjectNav 
          title="FlexableCare — UI Designer"
          info="Winter 2017-18 | New York, NY"
          skills="User interface design, Wireframing, Adobe Illustrator"
          left="/fullstackdev/"
          right="/comyoot/"
        >
        </ProjectNav>
        <Anime opacity={[0, 1]} translateY={'1em'} delay={(e, i) => 250}>
        <Col xs={12} sm={12} md={12} lg={{span:8, offset:4}} xl={{span:8, offset:4}} className="project-content">
          <Img fluid={data.imageOne.childImageSharp.fluid} alt="project cover" />
          <br /><br />
          <h2 className="project-h2">Background</h2>
          <p>Flexable provides pop up childcare at professional and personal events, offices, retail stores, and other businesses.</p>
          <p>I created high-fidelity mockups for Flexable's software system, designing for three users: the administrators, caregivers, and customer/clients. I worked directly with the developer and CEO of <a href="armanh.com" target="_blank" className="link">Ahez Co.</a>, creating a concept map to visualize the flow of software system for the three users. Below are several of the mockups. Feel free to <a href="mailto:cchang2@alumni.cmu.edu" className="link">contact me</a> to learn more!</p>
          <br /><br />
          <h2 className="project-h2">Mockups</h2>
          <Img fluid={data.imageTwo.childImageSharp.fluid} alt="register page screenshot"/><br /><br />
          <Img fluid={data.imageThree.childImageSharp.fluid} alt="event page screenshot"/><br /><br />
          <Img fluid={data.imageFour.childImageSharp.fluid} alt="quote summary screenshot"/><br /><br />
          <Img fluid={data.imageFive.childImageSharp.fluid} alt="caretaker profile screenshot"/><br /><br />
        </Col>
        </Anime>
      </Row>
    </Container>
  </Layout>
  )
}

export default FlexablePage
